.wrapper_content_pro{
    display: grid;
    width: 100%;
    grid-template-columns: repeat(auto-fit , minmax(175px,280px));
    justify-content: center;
    grid-column-gap: 1rem;
    grid-row-gap: 2rem;
    height: auto;
    padding-top: 2.5rem;
    padding-bottom: 4rem;
   
  }
  
.cardd{
  display: flex;
  flex-direction: column;
    width: 17rem;
    min-height: 16rem;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  }
.cardd .merge-rb{
    display: flex;
}

  
.cardd:hover{
    transition: 0.4s ease;
    transform: scale(1.02);
  
  }
  
.cardd .card_details{
  display: flex;
  flex: 2;
  color: black;
  flex-direction: column;
  justify-content: space-between;
  padding: 0.3rem 0.5rem;
  gap: 0.2rem;

  background-color: rgb(248 248 248);
  border-radius: 10px;
  height: 50%;
  }
  
.cardd .card_details .name-price{
    display: flex;
    justify-content: space-between;
    align-items: center;
  
  }
  
.cardd .card_image{

    width: 100%;

    display: flex;
    flex: 1;
    
  }
  
.cardd .card_image img{
    width: 100%;
    height: 11rem;
    object-fit:fill ;
    background-color: white;
    /* border-radius: 15px; */
    
  
   
  }
  
.addBtn{
    padding: 0.4rem 1rem;
    margin-bottom: 1rem;
    border-radius: 20px;
    border: none;
    background-color: #B06AB3;
    color: white;
    width: 60%;
    font-weight: 500;
    text-align: center;
  }  


.paginationComp{
  margin-bottom: 2rem;
  width: 100%;
  display: flex;
  justify-content: center;
}  